import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { MexExtensionElement } from "./MexExtensionElement";

export class ExecutionListener extends MexExtensionElement {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.Mex.ExecutionListener";
    NewInstance(): ExecutionListener {
        return new ExecutionListener(this.Model);
    }

    Class: string;
    Event: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Class = null;
        this.Event = null;;
    }

    ConvertToJsonObject(): any {
        let obj: any = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Event", this.Event);
        BpmnModelHelper.SetAttributeValue(obj, "Class", this.Class);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.Event = obj.Event;
        this.Class = obj.Class;
    }
}